class Map {
  constructor() {
    ymaps.ready(() => {
      this.addMap();
    });
  }

  addMap() {
    this.map = new ymaps.Map(document.querySelector('.map__map'), {
      center: [54.72320, 20.50786],
      zoom: 16,
      controls: ['zoomControl'],
      ignoreResize: true,
    });

    this.map.behaviors.disable([
      'drag',
    //   'multiTouch',
      'scrollZoom',
    ]);

    // const marker = new ymaps.Placemark([54.72320, 20.50786]);
    // this.map.geoObjects.add(marker);

    const logoMarker = new ymaps.Placemark(
      [54.7233, 20.5077], 
      {},
      {
        // Опции.
        // Необходимо указать данный тип макета.
        iconLayout: 'default#image',
        // Своё изображение иконки метки.
        // iconImageHref: 'img/spot.png',
        iconImageHref: 'img/marker--single.svg',
        // Размеры метки.
        iconImageSize: [58, 75],
        // Смещение левого верхнего угла иконки относительно
        // её "ножки" (точки привязки).
        iconImageOffset: [-29, -74]
    });

    // const logoMarker = new ymaps.Placemark(
    //   [54.72320, 20.50786],
    //   {
    //     // iconLayout: 'default#imageWithContent',
    //     iconLayout: 'default#image',
    //     iconImageHref: './img/icons/map-marker.svg',
    //     // iconContentLayout: MyIconContentLayout,
    //   },
    // );

    this.map.geoObjects.add(logoMarker);
  }
}

export default Map;

